import 'bootstrap/dist/css/bootstrap.min.css';
import { useEffect, useState } from 'react';
import { Table } from '../components/Table';

export default {
  title: 'WORKOUT/Series',
  component: Table,
};

export const Template = args => {
  const [_data, setData] = useState([]);
  const [_metadata, setMetadata] = useState([]);
  const [min, setMin] =use
  useEffect(() => {
    setData([
      { id: 1, date: '2022-11-23', repetitions: 15, weigth: 100 },
      { id: 2, date: '2022-11-23', repetitions: 10, weigth: 18 },
    ]);
    setMetadata({
      id: { name: 'id', type: 'text' },
      date: { name: 'date', type: 'date' },
      repetitions: { name: 'repetitions', type: 'number' },
      weigth: { name: 'weigth', type: 'number' },
    });
  }, []);

  const onItemChange = udpatedItem => {
    setData(
      _data.map(item => {
        if (item.id === udpatedItem.id) {
          return udpatedItem;
        }
        return item;
      }),
    );
  };

  const handleOnAdd = () => {
    if (_data.length < max) {
      console.log('Adding row!!!');
      // setData([
      //   ..._data,
      //   { ..._data[_data.length - 1], id: { value: _data.length + 1 }, editable: { value: true } },
      // ]);
    } else console.log('That is the max!!!');
  };

  const handleOnDelete = () => {
    if (_data.length > min) {
      console.log('We are deleting data');
      //setData(_data.filter(item => item.id.value !== _data.length));
    } else console.log('That is the min!!!');
  };

  return (
    <Table
      onItemChange={onItemChange}
      data={_data}
      metadata={_metadata}
      handleOnAdd={handleOnAdd}
      handleOnDelete={handleOnDelete}
      {...args}
    />
  );
};;

export const Monday = Template.bind({});
Monday.args = {
  title: 'Curl biceps',
  muscleGroup: 'Biceps',
  max: 5,
  min: 1,
  handleOnSave: data => {
    console.log('saving');
    console.log(data);
  },
};

